
html {
    scroll-behavior: smooth;
}


.header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: var(--z-fixed);
    background-color: var(--white-background)
}

.nav {
    height: calc((var(--header-height) + 1rem));

    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 1rem;
}

.nav_logo,
.nav_toggle {
    color: var(--title_color);
    font-weight: var(--font-medium);
}


.nav_logo{
    font-size: 20px;
    font-weight: var(--font-semi-bold);
}
.nav_logo:hover{
    color: var(--title-color-dark);
}

.nav_list{
    display: flex;
    column-gap: 2rem;
}

.nav_link{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: var(--small-font-size);
    color: var(--title_color);
    font-weight: var(--font-medium);
    transition: .3s;
}


.nav_icon,
.nav_close,
.nav_toggle{
    display: none;
}

/* ACTIVE LINK */

.active-link,
.nav_link:hover{
    color: var(--title-color-dark);
}


/* -----------------BREAKPOINTS --------------------- */

@media  screen and (max-width: 786px) {

    .header{
        top: initial;
        bottom: 0;

        border-top: 1px solid var(--title-color);
    }

    .nav{
        height: var(--header-height);
    }
    .nav_menu{
        position: fixed;
        bottom: -100%;
        left: 0;
        width: 100%;
        background-color: var(--white-background);
        padding: 2rem 1.5rem 4rem;
        box-shadow: 0 -5px 4px rgba(0,0,0,0.15);
        border-top: 1px solid var(--title-color);
        border-radius: 1.5rem 1.5rem 0 0;

        transition: .3s;
    }

    .show_menu{
        bottom: 0;
    }

    .nav_list{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem;
    }


    .nav_icon{
        font-size: 1rem;
    }

    .nav_close {
        position: absolute;
        right: 1.3rem;
        bottom: .5rem;
        font-size: 1.5rem;
        cursor: pointer;
    }

    .nav_close:hover{
        color: var(--title-color-dark);
    }

    .nav_toggle{
        font-size: 1.1rem;
        cursor: pointer;
    }
    

    .nav_icon,
    .nav_close,
    .nav_toggle{
        display: block;
    }
    
}


/* SMALL SCREENS --------------------------------------------------- */
@media  screen and (max-width: 350px) { 
    .nav_menu {
        padding: 2rem 0.25rem 4rem ;
    }

    .nav_list{
        column-gap: 0;
    }
}